import React from "react";
import styled from "styled-components";
import backgroundImg from "./images/largeBG.jpg";
import { motion } from "framer-motion";
import { Link } from "react-router-dom";

const MainSection = ({ drinkType, setDrinkType }) => {
  return (
    <Section>
      <Container
        key="container"
        className="container"
        animate={{ x: 0 }}
        initial={{ x: "100vw" }}
        transition={{ ease: "easeIn", duration: 1.5, delay: 1.2 }}
        exit={{
          opacity: 0,
          x: "-100vw",
          transition: { duration: 1.5 },
        }}
      >
        <div className="">
          <motion.h1
            animate={{ y: 0, opacity: 1 }}
            initial={{ y: "-100px", opacity: 0 }}
            transition={{ duration: 1, delay: 3 }}
          >
            drinks
          </motion.h1>
          <motion.h2
            animate={{ y: 0, opacity: 1 }}
            initial={{ y: "20rem", opacity: 0 }}
            transition={{ duration: 1, delay: 3 }}
          >
            For the thirsty ones.
          </motion.h2>
          <motion.p
            animate={{ y: 0, opacity: 1 }}
            initial={{ y: "20rem", opacity: 0 }}
            transition={{ duration: 1, delay: 3 }}
          >
            Pick the type of drink that you feel like drinking.
          </motion.p>

          <Link to="/drinks">
            <CheckBtn
              animate={{ y: 0, opacity: 1 }}
              initial={{ y: "20rem", opacity: 0 }}
              transition={{ duration: 1, delay: 3 }}
              onClick={() => setDrinkType("Cocktail")}
            >
              Cocktails
            </CheckBtn>
            <CheckBtn
              animate={{ y: 0, opacity: 1 }}
              initial={{ y: "20rem", opacity: 0 }}
              transition={{ duration: 1, delay: 3 }}
              onClick={() => setDrinkType("Ordinary_Drink")}
            >
              Ordinary Drinks
            </CheckBtn>
          </Link>
        </div>
      </Container>
    </Section>
  );
};

export default MainSection;

const Section = styled(motion.section)`
  background: url(${backgroundImg}) no-repeat center center fixed;
  background-size: cover;
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
`;

const Container = styled(motion.div)`
  background: rgba(0, 0, 0, 0.7);
  width: 100%;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fefefe;
  text-transform: uppercase;
  overflow: hidden;

  div {
    overflow: hidden;
    min-width: 35%;
    padding: 0.5em 0;
  }
  h1 {
    font-size: 7em;
  }
  h2 {
    font-size: 4em;
  }
  p {
    padding-left: 0.5em;
  }
  @media only screen and (max-width: 500px) {
    h1 {
      font-size: 3.5em;
    }
    h2 {
      font-size: 2em;
    }
  }
`;

const CheckBtn = styled(motion.button)`
  height: 4em;
  width: 15em;
  background: transparent;
  color: #fefefe;
  font-size: 1.2em;
  border: 1px solid gray;
  margin: 1em 0.5em;

  transition: all 0.3s ease;
  &:hover {
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transform: scale(1.1);
  }
  &:active {
    transform: scale(0.9);
  }
  @media only screen and (max-width: 500px) {
    font-size: 1em;
    height: 3em;
    width: 10em;
  }
`;
