import React, { useEffect, useState, useRef } from "react";
import styled from "styled-components";
import { motion } from "framer-motion";
import axios from "axios";
import useCollapse from "react-collapsed";

const CocktailDescription = ({ drink }) => {
  const drinkName = drink.strDrink.split(" ").join("_");
  const baseURL = "https://www.thecocktaildb.com/api/json/v1/1/search.php?s=";
  //Drink array with all the information
  const [drinkInfo, setDrinkInfo] = useState([]);
  const [isExpanded, setExpanded] = useState(false);
  //Collapse logic and props using useCollapse
  const { getCollapseProps, getToggleProps } = useCollapse({
    isExpanded,
    duration: 1500,
  });
  //Fetching the drink information
  useEffect(() => {
    axios
      .get(`${baseURL}${drinkName}`)
      .then((res) => {
        const result = res.data.drinks;
        result.forEach((resultArr) => setDrinkInfo(resultArr));
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);
  //Ingredients array so i can map over it and display them
  const [ingredientsArr, setIngredientsArr] = useState([]);
  const getIngredients = () => {
    setIngredientsArr([]);
    for (let i = 1; i <= 15; i++) {
      let ingredient = `strIngredient${i}`;
      let measurement = `strMeasure${i}`;
      if (drinkInfo[ingredient]) {
        let obj = {
          strIngredient: drinkInfo[ingredient],
          strMeasure: drinkInfo[measurement],
        };
        setIngredientsArr((prevArray) => [...prevArray, obj]);
      } else {
        return;
      }
    }
  };
  const elementRef = useRef(null);

  const scrollPositionHandler = () => {
    if (!elementRef) return;
    elementRef.current.scrollIntoView({ behavior: "smooth" });
  };
  // useEffect(() => {
  //   window.addEventListener("scroll", scrollPositionHandler);
  //   return () => {
  //     window.removeEventListener("scroll", scrollPositionHandler);
  //   };
  // }, []);

  //Image styling before and after the expand of the div
  const imgExpanded = {
    width: "60%",
    height: "auto",
    padding: "0 2em",
    transition: "all 1.5s ease",
  };
  const imgCollapsed = {
    width: "10em",
    height: "auto",
    transition: "all 1.5s ease",
  };
  return (
    <>
      <CocktailDescriptionContainer>
        <div>
          <h2>{drinkInfo.strDrink}</h2>

          <motion.img
            transition={{ duration: 1.5 }}
            src={drink.strDrinkThumb}
            alt={drink.strDrink}
            style={!isExpanded ? imgCollapsed : imgExpanded}
            ref={elementRef}
          />
          <CollapseBtn
            {...getToggleProps({
              onClick: (e) => {
                setExpanded((prevExpanded) => !prevExpanded);
                getIngredients();
                scrollPositionHandler();
              },
            })}
          >
            {isExpanded ? "Collapse" : "Check it!"}
          </CollapseBtn>
        </div>
        {isExpanded && (
          <CocktailDescriptionDiv {...getCollapseProps()}>
            {drinkInfo.strIBA && <p>{drinkInfo.strIBA}</p>}
            <div>
              <Headers>{drinkInfo.strAlcoholic}</Headers>
              <Headers>{drinkInfo.strGlass}</Headers>
            </div>
            <div>
              <Headers>Ingredients</Headers>
              <StyledList>
                {ingredientsArr &&
                  ingredientsArr.map((ingredient, index) => {
                    return (
                      <li key={index}>
                        {ingredient.strMeasure} {ingredient.strIngredient}
                      </li>
                    );
                  })}
              </StyledList>
            </div>
            <InstructionsContainer>
              <Headers>Method</Headers>

              <h5>{drinkInfo.strInstructions}</h5>
            </InstructionsContainer>
          </CocktailDescriptionDiv>
        )}
      </CocktailDescriptionContainer>
    </>
  );
};

export default CocktailDescription;

const CocktailDescriptionContainer = styled(motion.div)`
  height: auto;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  padding: 1em 0;
  transition: width 0.3s ease;
  div {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  h2 {
    margin: 1em 0;
    text-align: center;
  }
  img {
  }
`;

const CocktailDescriptionDiv = styled(motion.div)`
  width: 60vw;
  height: 30em;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: flex-start;
  @media only screen and (max-width: 480px) {
    width: 100%;
  }
`;
const CollapseBtn = styled(motion.button)`
  height: 2em;
  width: 9em;
  background: transparent;
  color: #fefefe;
  font-size: 1.2em;
  border: 1px solid gray;
  margin: 1em 0.5em;

  transition: all 0.3s ease;
  &:hover {
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transform: scale(1.1);
  }
  &:active {
    transform: scale(0.9);
  }
  @media only screen and (max-width: 500px) {
    font-size: 1em;
    height: 3em;
    width: 10em;
  }
`;
const Headers = styled(motion.p)`
  font-size: 1.5em;
  text-transform: uppercase;
  padding: 0.5em 0 0 0;
  margin-bottom: 0.2em;
  border-bottom: 2px solid #ffffff;
  font-weight: 600;
`;

const StyledList = styled(motion.ul)`
  margin: 1em 0;
  list-style-type: none;
  li {
    text-align: center;
  }
`;

const InstructionsContainer = styled.div`
  text-align: center;
`;
