import React, { useState, useEffect } from "react";
import styled from "styled-components";
import { motion } from "framer-motion";
import backgroundImg from "./images/largeBG.jpg";
import axios from "axios";
import CocktailDescription from "./CocktailDescription";
import { Link } from "react-router-dom";
import qs from "qs";
import { createBrowserHistory } from "history";
// import ReactLoading from "react-loading";
const Cocktails = ({ drinkType, setDrinkType }) => {
  const [drinksArr, setDrinksArr] = useState([]);
  const [arrFiltered, setArrFiltered] = useState([]);
  const history = createBrowserHistory();
  useEffect(() => {
    const filterParams = history.location.search.substr(1);
    const filtersFromParams = qs.parse(filterParams);
    if (filtersFromParams.drinkType) {
      setDrinkType(filtersFromParams.drinkType);
    }
  }, []);

  useEffect(() => {
    history.push();
  }, [drinkType]);

  useEffect(() => {
    axios
      .get(
        `https://www.thecocktaildb.com/api/json/v1/1/filter.php?c=${drinkType}`
      )
      .then((res) => {
        console.log(res);
        const result = [...res.data.drinks];
        // console.log(res.data);
        // console.log("Drink type is " + drinkType);
        setDrinksArr([...result]);
        setArrFiltered([...result]);
      })
      .catch((err) => {
        console.log("The error is: " + err);
      });
  }, []);
  const searchContent = (e) => {
    e.preventDefault();
    if (!e.target.value) {
      setArrFiltered([...drinksArr]);
    } else {
      setArrFiltered(
        drinksArr.filter((drink) => {
          if (drink.strDrink.toLowerCase().includes(e.target.value)) {
            return true;
          }
        })
      );
    }
  };

  const backBtnStyles = {
    position: "fixed",
    bottom: 0,
    right: 0,
    background: "#fefefe",
    color: "#030303",
    borderRadius: "50%",
    width: "3em",
    height: "3em",
  };
  return (
    <CocktailsSection>
      <CocktailsContainer
        key="cocktails-container"
        animate={{ y: 0, opacity: 1 }}
        initial={{ y: "-100vw", opacity: 0 }}
        transition={{ ease: "easeIn", duration: 1.5, delay: 3 }}
        exit={{ opacity: 0, y: "100vw", transition: { duration: 1.5 } }}
      >
        <FormDiv>
          <Link to="/">
            <SearchBtn>Homepage</SearchBtn>
          </Link>
          <form onSubmit={(e) => searchContent(e)}>
            <SearchInput
              type="text"
              placeholder="Search..."
              onChange={(e) => {
                searchContent(e);
              }}
            />
          </form>
        </FormDiv>

        {arrFiltered.map((drink) => {
          return (
            <CocktailCard key={drink.idDrink}>
              <CocktailDescription drink={drink}></CocktailDescription>
            </CocktailCard>
          );
        })}
      </CocktailsContainer>
      <SearchBtn
        style={backBtnStyles}
        onClick={() => window.scrollTo({ top: 0, behavior: "smooth" })}
      >
        &uarr;
      </SearchBtn>
    </CocktailsSection>
  );
};

export default Cocktails;

const CocktailsSection = styled(motion.section)`
  width: 100%;
  min-height: 100vh;
  background: url(${backgroundImg}) no-repeat center center fixed;
  background-size: cover;
  scroll-behavior: smooth;
`;

const FormDiv = styled(motion.div)`
  width: 100%;
  height: auto;
  margin: 0 auto;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  @media only screen and (max-width: 600px) {
    flex-direction: column;
    align-items: center;
  }
`;

const CocktailsContainer = styled(motion.div)`
  width: 100%;
  min-height: 100vh;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1em;
`;

const CocktailCard = styled(motion.div)`
  height: auto;
  min-width: 20em;
  margin: 1em 0;
  background: rgba(34, 33, 33, 0.3);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
  border-radius: 10px;
  color: #fefefe;

  @media only screen and (max-width: 330px) {
    min-width: 15em;
  }
`;
const SearchInput = styled(motion.input)`
  height: 3em;
  width: 20em;
  background: transparent;
  color: #fefefe;
  font-size: 1.2em;
  border: 1px solid gray;
  margin: 1em 0.5em;

  transition: all 0.3s ease;

  &:active {
    transform: scale(0.9);
  }
  @media only screen and (max-width: 500px) {
    font-size: 1em;
    height: 3em;
    width: 10em;
  }
`;

const SearchBtn = styled(motion.button)`
  height: 3em;
  width: 9em;
  background: transparent;
  color: #fefefe;
  font-size: 1.2em;
  border: 1px solid gray;
  margin: 1em 0.5em;

  transition: all 0.3s ease;
  &:hover {
    background-color: rgba(0, 0, 0, 0.5);
    cursor: pointer;
    transform: scale(1.1);
  }
  &:active {
    transform: scale(0.9);
  }
  @media only screen and (max-width: 500px) {
    font-size: 1em;
    height: 3em;
    width: 10em;
  }
`;
